.body{
    height: auto;
    overflow-y: hidden;
    min-height: 340px;
    min-width: 280px;
}
.body.loading .main::before{
    z-index: 999;
    content: 'LOADING...';
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    box-sizing: border-box;
    padding-top: calc((100vh - 400px)/2);
    width: 100%;
    height: 100vh;
    background: #FFF;
    color: #555;
    text-align: center;
    font-size: 36px;
    font-family: 'dosisB';
}
.body.loading .main::after{
    z-index: 999;
    content: '';
    display: block;
    position: absolute;
    top: calc((100vh - 250px)/2);
    left: calc((100vw - 250px)/2);
    overflow: hidden;
    width: 250px;
    height: 250px;
    box-sizing: border-box;
    border-radius: 100%;
    font-family: 'dosisB';
    background: lemonchiffon;
    background-image: url(../image/me.png);
    background-size: 126%;
    background-position: top;
    animation: float 2.5s ease-in-out infinite;
}
.floor{
    width: 100vw;
    height: 100vh;
}
.main{
    position: relative;
    background: beige;
    &::before{
        content: '';
        z-index: 9;
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url(../image/repeat_1.png);
        background-size: 800px auto;
    }
    &::after{
        content: '';
        z-index: 9;
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: url(../image/repeat_2.png);
        background-size: 800px auto;
        animation: movingBg2 3s infinite step-end;
    }
    &__text{
        padding-top: 15vh;
        box-sizing: border-box;
        user-select: none;
        position: relative;
        width: 100%;
        text-align: center;
        z-index: 10;
        font-family: 'dosisB';
        font-size: 150px;
        color: #FFF;
        text-shadow: 1px 1px 15px #000;
    }
}
.firstFloor{
    background: pink;
}

.secondFloor{
    background: lightblue;

}

.thirdFloor{
    background: skyblue;
}

.floorBtn__wrapper{
    position: fixed;
    top: 50%;
    left: 5%;
    z-index: 99;
}
@keyframes movingBg2{
    0%{
        background-position: 267px 267px;
    }
    50%{
        background-position: 534px 534px;
    }
    100%{
        background-position: 0px 0px;
    }
}
@keyframes float {
	0% {
		box-shadow: 0 10px 20px 0px rgba(0,0,0,0.4);
		transform: translatey(0px);
	}
	50% {
		box-shadow: 0 30px 20px 0px rgba(0,0,0,0.2);
		transform: translatey(-28px);
	}
	100% {
		box-shadow: 0 10px 20px 0px rgba(0,0,0,0.4);
		transform: translatey(0px);
	}
}