body { min-height: 1000px; height: auto; min-width: 860px; padding: 0; margin: 0; }

header { width: 100%; position: fixed; height: 100px; background: #FFF; z-index: 99; }

header .bar { position: relative; display: block; width: 100%; height: 98px; border-bottom-right-radius: 225px 15px !important; border-top: none !important; border-left: none !important; border-right: none !important; border-width: 3px; }

header .bar__gage { width: 0%; height: 100%; background-image: url("../image/wall_bg.png"); background-size: auto 100%; background-repeat: repeat-x; }

