header .bar .meIcon { position: absolute; bottom: 0; top: 0; display: inline-block; width: 50px; height: 100px; background-image: url("../image/charlie.png"); background-size: 100% auto; background-repeat: no-repeat; left: 0%; -webkit-transform: translateX(0%); -ms-transform: translateX(0%); transform: translateX(0%); }

.highlight__box { display: block; text-align: center; }

.highlight { display: inline-block; position: relative; }

.highlight > span { position: relative; z-index: 8; color: #444; }

.highlight::after { content: ''; position: absolute; display: block; top: 60%; left: 0%; width: 100%; height: 40%; opacity: 0.35; z-index: 1; }

.highlight.yellow::after { background: yellow; }

.highlight.green::after { background: #008D62; }

.highlight.blue::after { background: blue; }

.highlight.skyblue::after { background: #67c2ff; }

.highlight.red::after { background: red; }

.calendar { font-family: 'Coming Soon', cursive; table-layout: fixed; min-width: 800px; width: 100% !important; }

.calendar__title { font-weight: bold; font-size: 32px; margin: 20px 0px; }

.calendar__theadText { margin-right: 15px; font-size: 30px; }

.calendar__thead { text-align: left; line-height: 72.5px; width: 1300px; margin: 60px auto 30px; }

.calendar__btnWrapper { width: 60%; display: inline-block; }

.calendar__thead .calendar__btnWrapper > div { display: inline-block; }

.calendar__day tr th { text-align: center; }

.calendar__date td .date__wrapper { min-height: 90px; height: auto; min-width: 50px; }

.calendar__date td .date__text { -webkit-user-select: none; -ms-user-select: none; user-select: none; position: relative; display: inline-block; border-radius: 100%; text-align: center; font-weight: bold; width: 30px; height: 30px; font-size: 18px; line-height: 32px; }

.calendar__date td.date.on .date__text { background: #abdcfb; color: #FFF !important; text-shadow: 1px 1px 1px #111; cursor: pointer; }

.calendar__date td .lastMonth__date, .calendar__date td .nextMonth__date { opacity: 0.4; width: 30px; height: 30px; font-size: 18px; line-height: 32px; }

.schedule__wrapper { position: relative; width: 100%; }

.schedule__input { margin-left: 6px; width: 100%; max-height: 30px; font-size: 16px; line-height: 30px; font-family: 'NanumBarunpen'; }

.schedule__fixed { position: relative; border-bottom-left-radius: 225px 15px !important; border-top: none !important; border-left: none !important; border-right: none !important; position: relative; display: -webkit-box; display: -webkit-flex; display: -ms-flexbox; display: flex; -webkit-box-align: center; -webkit-align-items: center; -ms-flex-align: center; align-items: center; width: 100%; font-family: 'NanumBarunpen'; padding-left: 6px; }

.schedule__fixed .text { z-index: 8; width: 100%; box-sizing: border-box; padding-right: 15px; font-size: 16px; line-height: 23px; display: inline-block; overflow: hidden; text-overflow: ellipsis; white-space: nowrap; }

.schedule__fixed .text:hover { cursor: text; }

.schedule__fixed::before { content: '✔'; position: relative; z-index: 2; display: inline-block; font-size: 16px; margin-right: 5px; line-height: 23px; }

.schedule__delete { z-index: 9; position: absolute; right: 0px; font-size: 16px; margin-left: 5px; cursor: pointer; }

html::after { content: '🚫 이 페이지는 표준 브라우저만을 지원합니다.'; display: block; width: 100vw; height: 100vh; position: absolute; top: 0; left: 0; background: #FFF; color: #444; font-size: 20px; text-align: center; line-height: 99vh; font-family: 'NanumBarunpen'; }

body { display: none; }

@media screen and (-webkit-min-device-pixel-ratio: 0) { html::after { content: unset; }
  body { display: block; } }

