@keyframes bounce { 0% { transform: translate3d(0, 0px, 0) scale(0.2);
    opacity: 0; }
  20% { transform: translate3d(0, 0, 0) scale(1.4); }
  40% { transform: translate3d(0, -30px, 0); }
  43% { transform: translate3d(0, -30px, 0); }
  53% { transform: translate3d(0, 0, 0);
    opacity: 1; }
  70% { transform: translate3d(0, -15px, 0); }
  80% { transform: translate3d(0, 0, 0) scale(1); }
  90% { transform: translate3d(0, -15px, 0); }
  100% { transform: translate3d(0, 0, 0);
    opacity: 1;
    /* box-shadow: 0 5px 15px 0px rgba(0,0,0,0.6); */ } }

.bounce { opacity: 0; position: relative; }

.bounce.on { display: inline-block; animation-name: bounce; animation-duration: 0.6s; animation-iteration-count: 1; animation-fill-mode: forwards; }

.delay1 { animation-delay: 0.1s; }

.delay2 { animation-delay: 0.2s; }

.delay3 { animation-delay: 0.3s; }

