.body { height: auto; overflow-y: hidden; min-height: 340px; min-width: 280px; }

.body.loading .main::before { z-index: 999; content: 'LOADING...'; display: block; position: absolute; top: 0; left: 0; box-sizing: border-box; padding-top: calc((100vh - 400px)/2); width: 100%; height: 100vh; background: #FFF; color: #555; text-align: center; font-size: 36px; font-family: 'dosisB'; }

.body.loading .main::after { z-index: 999; content: ''; display: block; position: absolute; top: calc((100vh - 250px)/2); left: calc((100vw - 250px)/2); overflow: hidden; width: 250px; height: 250px; box-sizing: border-box; border-radius: 100%; font-family: 'dosisB'; background: lemonchiffon; background-image: url(../image/me.png); background-size: 126%; background-position: top; animation: float 2.5s ease-in-out infinite; }

